import './FileDrop.css';

import React from 'react';

function FileDrop(props) {
  const element = document.body;

  const handleDrag = e => {
   e.preventDefault();
   e.stopPropagation();
 };

 const handleDragIn = e => {
   e.preventDefault();
   e.stopPropagation();
 };

 const handleDragOut = e => {
   e.preventDefault();
   e.stopPropagation();
 };

 const handleDrop = e => {
   e.preventDefault();
   e.stopPropagation();
   if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
     uploadFiles([...e.dataTransfer.files]);
     e.dataTransfer.clearData();
   }
 };

 const uploadFiles = files => {
    let url = '/upload'
    let formData = new FormData()

    files.forEach(file => {
      formData.append('files', file);
    });

    fetch(url, {
      method: 'POST',
      body: formData
    });
 }

 React.useEffect(() => {
    element.addEventListener('dragenter', handleDragIn);
    element.addEventListener('dragleave', handleDragOut);
    element.addEventListener('dragover', handleDrag);
    element.addEventListener('drop', handleDrop);
    return () => {
      element.removeEventListener('dragenter', handleDragIn);
      element.removeEventListener('dragleave', handleDragOut);
      element.removeEventListener('dragover', handleDrag);
      element.removeEventListener('drop', handleDrop);
    };
  });

  return (
    <>
      {props.children}
    </>
  );
}

export default FileDrop;
